@charset "UTF-8";
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  outline: none; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

* {
  box-sizing: border-box; }

a {
  text-decoration: none;
  color: #2a5b90; }

a.dashed {
  text-decoration: none;
  border-bottom: 1px dashed; }

a.dashed:hover {
  border-bottom: none; }

b,
strong {
  font-weight: 700; }

i,
em {
  font-style: italic; }

input[type="text"],
input[type="password"],
textarea {
  border: 1px solid #c8c8c8;
  background: #fff;
  padding: 7px 10px;
  font-family: 'Trebuchet MS';
  font-weight: 400;
  outline: none;
  border-radius: 2px; }

input[type="text"].error,
input[type="password"].error,
textarea.error {
  border: 1px solid #802a2a;
  background: #ecdfdf; }

input[type="button"],
input[type="submit"],
input[type="reset"] button {
  font-weight: 400;
  outline: none;
  border: none;
  cursor: pointer; }

textarea {
  resize: none;
  font-family: 'Trebuchet MS';
  font-weight: 400;
