#content ul {
  padding: 0 0 15px 15px; }

#content ul ul,
#content ul ul ul {
  padding: 5px 0 7px 15px; }

#content ul li {
  color: #393939;
  font-size: 14px;
  position: relative;
  padding-left: 20px; }

#content ul li:before {
  content: '―';
  margin-right: 7px;
  color: #aebac8;
  font-size: 15px;
  line-height: 15px;
  position: absolute;
  top: 0;
  left: 0; }

#content ol {
  padding: 0 0 15px 15px;
  list-style-position: inside;
  list-style-type: none;
  counter-reset: list1; }

#content ol ol,
#content ol ol ol {
  padding: 5px 0 7px 15px; }

#content ol ol {
  counter-reset: list2; }

#content ol ol ol {
  counter-reset: list3; }

#content ol li {
  color: #4d4d4d;
  font-size: 15px;
  position: relative;
  padding-left: 25px; }

#content ol li:before {
  counter-increment: list1;
  content: counter(list1) ". ";
  color: #a7aebb;
  position: absolute;
  top: 0;
  left: 0;
  font-weight: 700;
  font-style: italic; }

#content ol ol li:before {
  counter-increment: list2;
  content: counter(list1) "." counter(list2) ". "; }

#content ol ol ol li:before {
  counter-increment: list3;
  content: counter(list1) "." counter(list2) "." counter(list3) ". "; }

#content table {
  border-collapse: collapse;
  margin-bottom: 25px;
  font-size: 15px;
  color: #4d4d4d;
  border-bottom: 1px solid #ccd8e6;
  border-top: 2px solid #ccd8e6; }

#content table caption {
