  font-style: italic;
  padding: 5px 0;
  color: #7e7e7e; }

#content table tr:nth-child(2n+1) {
  background: #edf1f6; }

#content table td,
#content table th {
  padding: 12px 30px; }

#content table th {
  color: #747474;
  font-weight: 700; }

.clearfix:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0; }

