.icatalog {
	margin-bottom: 50px;
	&--inside {
		width: 100%;
		margin: 32px 0 0;
		&:first-child {
			margin-top: 0; }
		.ibanner {
			margin-bottom: 27px;
			@media (max-width: 970px) {
				display: none; }
			&:last-child {
				margin-bottom: 0; } }
		.icatalog {
			&__element {
				&:last-child {
					margin-bottom: 0; } } } }
	&__cities {
		font-size: 23px;
		font-weight: bold;
		color: $black;
		margin-bottom: 13px; }

	&__cities-link {
		font-size: 23px;
		font-weight: bold;
		color: $blue;
		border-bottom: 1px dotted $blue;
		transition: .5s border-color;
		display: inline-block;
		margin-left: 7px;

		&:hover {
			border-color: transparent; } }

	&__list {
		display: flex;
		flex-direction: column; }

	&__element {
		display: flex;
		flex-direction: row;
		max-height: 230px;
		margin-bottom: 27px;
		box-shadow: 0 6px 29px rgba(#434355, 0);
		transition: box-shadow .5s;
		&:hover {
			box-shadow: 0 6px 29px rgba(#434355, .24); } }

	&__photos {
		max-width: 380px; }

	&__info {
		width: 100%;
		border: 1px solid #e0e0e0;
		border-left: none;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		padding: 36px 40px; }

	&__info-top {
		display: flex; }

	&__info-bottom {
		display: flex;
		justify-content: space-between; }

	&__car {
		font-size: 20px;
		color: $black;
		font-weight: 500;
		margin-bottom: 10px;
		border-bottom: 1px solid $black;
		transition: border .5s;
		&:hover {
			border-bottom-color: transparent; } }

	&__char {
		font-size: 14px;
		line-height: 20px;
		font-weight: normal;
		color: rgba(57,57,57,0.5); }

	&__autocenter {
		font-size: 14px;
		line-height: 20px;
		font-weight: normal;
		color: rgba(57,57,57,0.5); }

	&__info-col {
		&--45per {
			width: 45%; }

		&--35per {
			width: 35%; } }

	&__year {
		width: 15%;
		white-space: nowrap; }

	&__run {
		width: 15%;
		text-align: right;
		white-space: nowrap; }

	&__autocenter-adress {
		font-size: 14px;
		line-height: 20px;
		font-weight: normal;
		color: rgba(57,57,57,0.5); }

	&__cost {
		font-size: 20px;
		line-height: 20px;
		font-weight: 500;
		color: $black;
		margin-bottom: 10px; }

	&__old-cost {
		font-size: 16px;
		line-height: 20px;
		font-weight: normal;
		color: rgba(53,64,76,0.5);
		text-decoration: line-through;
		margin-bottom: 10px; }

	&__credit {
		display: flex;
		flex-direction: row;
		font-size: 14px;
		line-height: 20px;
		font-weight: {}
		color: $red; }

	&__credit-modal {
		position: relative;
		transition: .5s background;
		margin-left: 10px;
		width: 19px;
		height: 19px;
		background-image: url('data:image/png;base64,');

		&:hover {
			background-image: url('data:image/png;base64,');

			.icatalog__credit-over {
				visibility: visible;
				opacity: 1;
				z-index: 100; } } }

	&__credit-over {
		position: absolute;
		bottom: calc(100% + 13px);
		left: 50%;
		width: 567px;
		transform: translateX(-50%);
		background-color: $white;
		padding: 40px;
		box-shadow: 0 5px 35.3px 2.7px rgba(0, 0, 0, 0.11);
		visibility: hidden;
		opacity: 0;
		transition: .5s visibility, .5s opacity; }

	&__credit-title {
		font-size: 20px;
		line-height: 24px;
		font-weight: 400;
		color: $black;
		margin-bottom: 15px; }

	&__credit-text {
		font-size: 14px;
		line-height: 20px;
		color: $black; }

	&__btn {
		align-self: flex-end; }

	.swiper-pagination {
		display: flex;
		flex-direction: row;
		align-items: stretch;
		padding-left: 10px;
		padding-right: 10px;
		bottom: 13px; }

	.swiper-pagination-bullet {
		height: 3px;
		border: none;
		width: 100%;
		background-color: #e3e3e3;
		border-radius: 2px; }

	.swiper-pagination-bullet-active {
		background-color: $red; }

	&__show-more {
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 100%;
		height: 85px;
		background-color: $blue;
		text-align: center;

		&:hover {
			span {
				border-color: transparent; } }

		span {
			align-self: center;
			display: inline-block;
			font-size: 14px;
			line-height: 20px;
			color: $white;
			border-bottom: 1px solid $white;
			transition: .5s border-color; } } }

@media (max-width: 1280px) {
	.icatalog {
		&__credit-over {
			left: auto;
			right: 0;
			bottom: calc(100% + 10px);
			transform: translateX(0); }

		&__photos {
			max-width: 300px; }

		&__photos-slide {
			img {
				max-width: 100%; } }

		&__element {
			max-height: 182px; }

		&__info {
			padding: 15px 20px; } } }

@media (max-width: 970px) {
	.icatalog {
		&__list {
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;
			justify-content: space-between; }

		&__element {
			width: calc(100% / 2 - 15px);
			flex-direction: column;
			max-height: inherit; }

		&__photos {
			max-width: inherit; }

		&__photos-slide {
			a {
				width: 100%;
				display: inline-block;
				vertical-align: top;
				font-size: 0;
				line-height: 0; }

			img {
				width: 100%; } }

		&__info-top {
			flex-direction: column; }

		&__info-col {
			&--45per {
				width: 100%;
				margin-bottom: 10px; }

			&--35per {
				width: 100%;
				margin-bottom: 10px; } }

		&__year {
			width: 100%; }

		&__run {
			width: 100%;
			text-align: left;
			margin-bottom: 10px; }

		&__info-bottom {
			flex-direction: column;
			justify-content: flex-start; }

		&__autocenter {
			margin-bottom: 10px; }

		&__btn {
			align-self: flex-start; }

		&__info {
			border: 1px solid #e0e0e0;
			border-top: none; }

		&__credit-over {
			transform: translateX(-30%);
			right: auto;
			width: 310px;
			padding: 30px 20px; } } }

@media (max-width: 767px) {
	.icatalog {
		&__element {
			width: 100%; } } }

@media (max-width: 393px) {
	.icatalog {
		&__credit-over {
			width: 240px;
			transform: translateX(-42%); } } }
